import axios from "axios";

export default async function fetchMoviesById(movieId) {
  const params = new URLSearchParams({
    api_key: "2f202abcab3fe0934220a17698275697",
  });

  try {  
    const response = await axios.get(`
    https://api.themoviedb.org/3/movie/${movieId}?${params}`);
    return response.data;
  } catch (error) {
    console.log(`${error.name}: ${error.message}`);
  };
};

/* Wywoywanie funkcji:
fetchTrendyMovies(); - wywoanie domylne tj. page = 1 i wyszukiwanie popularnych z tygodnia
fetchTrendyMovies(5); - wywoanie strony 5 i wyszukiwanie popularnych z tygodnia
fetchTrendyMovies(3, "day"); - wywoanie strony 3 i wyszukiwanie popularnych z dnia
fetchTrendyMovies(undefined, "day"); - wywoanie strony domylnej tj. page = 1 i wyszukiwanie popularnych z dnia
*/